import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import moment from 'moment';
import { withRouter } from 'react-router';
import { compose } from 'recompose';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
import AvatarImage from '../../../components/AvatarImage';
import styles from './styles';

const renderBadge = (deadline) => {
  if (deadline) {
    const deadlineLabel = moment(deadline).format('MMM D');
    const startOfToday = moment().startOf('day');
    const startOfDeadline = moment(deadline).startOf('day');
    const isToday = startOfDeadline.isSame(startOfToday, 'day');
    const isTomorrow = startOfDeadline.diff(startOfToday, 'days') === 1;
    const isExpired = startOfDeadline.isBefore(startOfToday, 'day');
    if (isToday) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipToday} />;
    }
    if (isTomorrow) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipTomorrow} />;
    }
    if (isExpired) {
      return <Chip label={deadlineLabel} size="small" style={styles.cardChipExpired} />;
    }
    return <Chip label={deadlineLabel} size="small" style={styles.cardChipFarFromExpiration} />;
  }
  return null;
};

const TicketCard = memo(({
  ticket, loading, onClickCard
}) => {
  const onClick = () => {
    if (!loading) {
      onClickCard(ticket);
    }
  };
  const requesterName = `${_get(ticket, 'requester.name')} ${_get(ticket, 'requester.lastname', '')}`;
  const assignee = _get(ticket, 'assignee');
  const assigneeName = `${_get(assignee, 'name')} ${_get(assignee, 'lastname', '')}`;
  const assignePhotoUrl = _get(assignee, 'photo_url');
  const createdAtLabel = _get(ticket, 'created_at') ? `${moment(ticket.created_at).fromNow()} por ` : '';
  const ticketTitle = _get(ticket, 'title', '');
  const ticketCategory = _get(ticket, 'category.title', '');
  return (
    <React.Fragment>
      <Card style={styles.card} onClick={onClick}>
        <CardContent style={styles.cardContent}>
          {!loading && renderBadge(_get(ticket, 'deadline'))}
          <Typography variant="subtitle1" style={styles.cardTitle}>
            {loading ? <Skeleton animation="wave" /> : <b>{ticketTitle}</b>}
          </Typography>
          {
            loading ?
              <Skeleton animation="wave" width={150} />
              :
              <Typography variant="body2" color="textSecondary" component="small">
                {createdAtLabel}
                <b>{requesterName}</b>
              </Typography>
          }
          <div style={styles.cardAction}>
            <div style={styles.cardAvatar}>
              {
                loading ?
                  <Skeleton animation="wave" width={100} />
                  :
                  assignee &&
                    <React.Fragment>
                      <AvatarImage name={assigneeName} photoUrl={assignePhotoUrl} />
                      <Typography variant="subtitle1" style={styles.cardAvatarName}>
                        <small>{assigneeName}</small>
                      </Typography>
                    </React.Fragment>
              }
            </div>
            <Typography variant="body2" color="textSecondary" component="small">
              {loading ? <Skeleton animation="wave" width={50} /> : ticketCategory}
            </Typography>
          </div>
        </CardContent>
      </Card>
    </React.Fragment>
  );
});

TicketCard.propTypes = {
  ticket: PropTypes.object,
  loading: PropTypes.bool,
  onClickCard: PropTypes.func
};

export default compose(
  withRouter
)(TicketCard);
