/* global window */
import React, { memo } from 'react';
import {
  compose, withState, withPropsOnChange,
  lifecycle, withHandlers
} from 'recompose';
import PropTypes from 'prop-types';
import moment from 'moment';
import MomentUtils from '@date-io/moment';
import {
  sortBy as _sortBy, get as _get, find as _find,
  map as _map, size as _size, isEmpty as _isEmpty, mapValues as _mapValues
} from 'lodash';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery, Grid, Typography,
  Dialog, DialogContent, DialogActions, DialogTitle, IconButton,
  FormControl, FormControlLabel, FormHelperText, Switch, TextField, Button
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import Loading from '../../../components/Loading';
import TicketsService from '../../../services/tickets';
import TicketCommentsService from '../../../services/ticket-comments';
import withRequest from '../../../utils/with-request';
import Tracker from '../../../utils/tracker';
import Session from '../../../utils/session';
import TicketsEnum from '../../../enums/tickets';
import { MEMBER_ROLE, AGENT_ROLES } from '../../../enums/user-roles';
import TicketComment from './TicketComment';
import config from '../../../config';
import styles from './styles';

const {
  PRIORITIES, STATUSES, KANBAN_STATUSES,
  STATUS_REJECTED, STATUS_ARCHIVED, HIDDEN_STATUSES
} = TicketsEnum;
const NO_ASSIGNEE_LABEL = 'Nenhum responsvel';

const TicketModal = memo(({
  classes, assignees, categories,
  open, onClose, setCurrentTicketState,
  currentTicket, formData: { validationErrors: errors },
  ticketLoading,
  updateTicketFetch, updateTicketLoading,
  ticketCommentsData, ticketCommentsLoading,
  commentMessage, setCommentMessage, setCloseModal,
  sendTicketCommentsFetch, sendTicketCommentsLoading,
  moveTicketFetch, moveTicketLoading
}) => {
  if (!open) return null;
  const enterpriseUser = Session.getUserData();
  const locations = _sortBy(_get(enterpriseUser, 'member.locations', []), ['name']);
  const memberRole = _get(enterpriseUser, 'member.role', MEMBER_ROLE);
  const isAgentUser = AGENT_ROLES.includes(memberRole);
  const comments = _sortBy(_get(ticketCommentsData, 'ticket_comments', []), ['created_at']).reverse();
  const requesterId = _get(currentTicket, 'requester.id');
  const memberId = _get(enterpriseUser, 'member.id');
  let statusValue = _get(currentTicket, 'status');
  const isHiddenStatus = HIDDEN_STATUSES.includes(statusValue);
  const isMemberRequester = (requesterId && memberId && memberId === requesterId);
  const canEditForm = isAgentUser && !isHiddenStatus;
  const canComment = canEditForm || (isHiddenStatus ? false : isMemberRequester);
  const isCommentDisabled = !canComment || sendTicketCommentsLoading || ticketCommentsLoading;
  const isFormLoading = updateTicketLoading || moveTicketLoading || ticketLoading;
  const isFormDisabled = !canEditForm;
  const lockSendMessageButton = isCommentDisabled || _isEmpty(_get(commentMessage, 'content', ''));
  const requester = `${_get(currentTicket, 'requester.name', '')} ${_get(currentTicket, 'requester.lastname', '')}`;
  const createdAt = _get(currentTicket, 'created_at') ? moment(currentTicket.created_at).fromNow() : '';
  const code = _get(currentTicket, 'code', '');
  let priorityValue = _isEmpty(_get(currentTicket, 'priority')) ? '' : _get(currentTicket, 'priority');
  let locationValue = _get(currentTicket, 'location.id');
  let categoryValue = _get(currentTicket, 'category.id');
  let assigneeValue = _get(currentTicket, 'assignee.id');
  let deadlineValue = _get(currentTicket, 'deadline', null);
  if (isFormDisabled) {
    priorityValue = _get(_find(PRIORITIES, p => p.key === priorityValue), 'label', '');
    locationValue = _get(_find(locations, b => b.id === locationValue), 'name', '');
    categoryValue = _get(_find(categories, c => c.id === categoryValue), 'title', '');
    statusValue = _get(_find(STATUSES, s => s.key === statusValue), 'label', '');
    deadlineValue = _isEmpty(deadlineValue) ? '' : moment(deadlineValue).format('DD/MM/YYYY');
    const assignee = _find(assignees, a => a.id === assigneeValue);
    assigneeValue = assignee ? `${_get(assignee, 'name', '')} ${_get(assignee, 'lastname', '')}` : NO_ASSIGNEE_LABEL;
  }
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const handleClose = () => {
    Tracker.hit('TicketModal - Closed');
    setCloseModal(true);
    onClose();
  };
  const handleChangeTicketState = (attrs) => {
    if (canEditForm) {
      setCloseModal(false);
      setCurrentTicketState(attrs);
      updateTicketFetch(true);
    }
  };
  const handleReject = () => {
    if (canEditForm) {
      if (window.confirm('Tem certeza em rejeitar esse ticket?')) {
        const params = { variables: { id: currentTicket.id, status: STATUS_REJECTED } };
        setCloseModal(true);
        moveTicketFetch(true, '', null, params);
      }
    }
  };
  const handleArchive = () => {
    if (canEditForm) {
      if (window.confirm('Tem certeza em arquivar esse ticket?')) {
        const params = { variables: { id: currentTicket.id, status: STATUS_ARCHIVED } };
        setCloseModal(true);
        moveTicketFetch(true, '', null, params);
      }
    }
  };
  const handleChangeStatus = (status) => {
    if (canEditForm) {
      setCloseModal(false);
      const params = { variables: { id: currentTicket.id, status } };
      setCurrentTicketState({ status });
      moveTicketFetch(true, '', null, params);
    }
  };
  const handleChangeBuilding = (locationId) => {
    if (canEditForm) {
      setCloseModal(false);
      const params = { variables: { id: currentTicket.id, location_id: locationId } };
      setCurrentTicketState({ location: { id: locationId } });
      moveTicketFetch(true, '', null, params);
    }
  };
  const handleSendComment = () => {
    if (canComment) {
      setCloseModal(false);
      sendTicketCommentsFetch(true);
    }
  };
  return (
    <Dialog open={open} fullScreen={fullScreen} onClose={handleClose} keepMounted fullWidth maxWidth="md">
      <Grid container>
        <Grid item xs={12} sm={12} md={5} className={classes.bgGray}>
          <DialogTitle>
            {_get(currentTicket, 'title', '')}
            <br />
            <Typography variant="body2" color="textSecondary" component="small">
              {ticketLoading ? '' : `#${code} - ${createdAt} por `}
              <b>{requester}</b>
            </Typography>
            <IconButton className={classes.closeButton} onClick={handleClose}>
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <DialogContent className={classes.ticketContent}>
            <FormControl fullWidth margin="normal" error={!!errors.status}>
              <TextField
                error={!!errors.status}
                label="Status"
                select={canEditForm}
                value={statusValue}
                disabled={isFormLoading}
                InputProps={{ readOnly: isFormDisabled }}
                InputLabelProps={{ shrink: true }}
                SelectProps={{ native: true }}
                onChange={e => handleChangeStatus(e.target.value)}
                variant="outlined"
              >
                {KANBAN_STATUSES.map(s => <option value={s.key} key={s.key}>{s.label}</option>)}
              </TextField>
              {errors.status && <FormHelperText>{errors.status}</FormHelperText>}
            </FormControl>
            {
              _size(locations) > 1 &&
                <FormControl fullWidth margin="normal" error={!!errors.location_id}>
                  <TextField
                    error={!!errors.location_id}
                    label="Local"
                    select={canEditForm}
                    value={locationValue}
                    disabled={isFormLoading}
                    InputProps={{ readOnly: isFormDisabled }}
                    InputLabelProps={{ shrink: true }}
                    SelectProps={{ native: true }}
                    onChange={e => handleChangeBuilding(e.target.value)}
                    variant="outlined"
                  >
                    {locations.map(b => <option value={b.id} key={b.id}>{b.name}</option>)}
                  </TextField>
                  {errors.location_id && <FormHelperText>{errors.location_id}</FormHelperText>}
                </FormControl>
            }
            <FormControl fullWidth margin="normal" error={!!errors.category_id}>
              <TextField
                error={!!errors.category_id}
                label="Categoria"
                select={canEditForm}
                value={categoryValue}
                disabled={isFormLoading}
                InputProps={{ readOnly: isFormDisabled }}
                SelectProps={{ native: true }}
                InputLabelProps={{ shrink: true }}
                onChange={e => handleChangeTicketState({ category: { id: e.target.value } })}
                variant="outlined"
              >
                {categories.map(c => <option key={c.id} value={c.id}>{c.title}</option>)}
              </TextField>
              {errors.category_id && <FormHelperText>{errors.category_id}</FormHelperText>}
            </FormControl>
            <FormControl fullWidth margin="normal" error={!!errors.assignee_id}>
              <TextField
                error={!!errors.assignee_id}
                label="Responsvel"
                select={canEditForm}
                value={assigneeValue}
                disabled={isFormLoading}
                InputProps={{ readOnly: isFormDisabled }}
                SelectProps={{ native: true }}
                InputLabelProps={{ shrink: true }}
                onChange={e => handleChangeTicketState({ assignee: { id: e.target.value } })}
                variant="outlined"
              >
                <option value="">{NO_ASSIGNEE_LABEL}</option>
                {assignees.map(a => <option key={a.id} value={a.id}>{`${a.name} ${a.lastname}`}</option>)}
              </TextField>
              {errors.assignee_id && <FormHelperText>{errors.assignee_id}</FormHelperText>}
            </FormControl>
            <Grid container direction="row" justify="center" spacing={1}>
              <Grid item xs={6}>
                <FormControl fullWidth margin="normal" error={!!errors.deadline}>
                  {
                    isFormDisabled ?
                      <TextField
                        error={!!errors.deadline}
                        label="Prazo"
                        value={deadlineValue}
                        disabled={isFormLoading}
                        InputProps={{ readOnly: isFormDisabled }}
                        InputLabelProps={{ shrink: true }}
                        variant="outlined"
                      />
                      :
                      <MuiPickersUtilsProvider
                        libInstance={moment}
                        utils={MomentUtils}
                        locale={config.locale}
                      >
                        <DatePicker
                          autoOk
                          label="Prazo"
                          clearable
                          animateYearScrolling
                          disabled={isFormLoading}
                          InputProps={{ readOnly: isFormDisabled }}
                          invalidDateMessage="Formato invlido"
                          minDateMessage=""
                          variant="outlined"
                          inputVariant="outlined"
                          InputLabelProps={{ shrink: true }}
                          value={deadlineValue}
                          onChange={e => handleChangeTicketState({ deadline: e })}
                          minDate={new Date()}
                          format="DD/MM/YYYY"
                          cancelLabel="Cancelar"
                          clearLabel="Limpar"
                        />
                      </MuiPickersUtilsProvider>
                  }
                  {errors.deadline && <FormHelperText>{errors.deadline}</FormHelperText>}
                </FormControl>
              </Grid>
              <Grid item xs={6}>
                <FormControl fullWidth margin="normal" error={!!errors.priority}>
                  <TextField
                    error={!!errors.priority}
                    select={canEditForm}
                    label="Prioridade"
                    value={priorityValue}
                    disabled={isFormLoading}
                    InputProps={{ readOnly: isFormDisabled }}
                    InputLabelProps={{ shrink: true }}
                    SelectProps={{ native: true }}
                    onChange={e => handleChangeTicketState({ priority: e.target.value })}
                    variant="outlined"
                  >
                    {PRIORITIES.map(p => <option value={p.key} key={p.key}>{p.label}</option>)}
                  </TextField>
                  {errors.priority && <FormHelperText>{errors.priority}</FormHelperText>}
                </FormControl>
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions className={classes.ticketActions}>
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={handleReject}
              disabled={isFormDisabled}
            >
              REJEITAR
            </Button>
            <Button
              color="default"
              size="large"
              variant="contained"
              onClick={handleArchive}
              disabled={isFormDisabled}
            >
              ARQUIVAR
            </Button>
          </DialogActions>
        </Grid>
        <Grid item xs={12} sm={12} md={7}>
          <DialogTitle>Atividade</DialogTitle>
          <DialogContent className={classes.commentContent}>
            <div className={classes.commentArea}>
              {
                ticketCommentsLoading ?
                  <Loading />
                  :
                  _map(comments, c => <TicketComment key={c.id} comment={c} />)
              }
            </div>

            <div className={classes.commentHeaderContainer}>
              <Typography variant="h6">Comentar</Typography>
              {
                isAgentUser &&
                  <FormControlLabel
                    className={classes.isInternalSwitch}
                    control={
                      <Switch
                        checked={commentMessage.isInternal}
                        onChange={() => setCommentMessage({
                          ...commentMessage, isInternal: !commentMessage.isInternal
                        })}
                        color="primary"
                      />
                    }
                    label="Interno"
                    disabled={isCommentDisabled}
                  />
              }
            </div>
            <FormControl fullWidth margin="normal">
              <TextField
                variant="filled"
                multiline
                rows={3}
                rowsMax={3}
                placeholder="Escreva seu comentrio aqui..."
                InputLabelProps={{ shrink: true }}
                value={commentMessage.content}
                onChange={e => setCommentMessage({ ...commentMessage, content: e.target.value })}
                disabled={isCommentDisabled}
              />
            </FormControl>
          </DialogContent>
          <DialogActions className={classes.commentActions}>
            <Button
              color="primary"
              size="large"
              variant="contained"
              onClick={handleSendComment}
              disabled={lockSendMessageButton}
            >
              ENVIAR
            </Button>
          </DialogActions>
        </Grid>
      </Grid>
    </Dialog>
  );
});

TicketModal.propTypes = {
  open: PropTypes.bool,
  classes: PropTypes.object,
  onClose: PropTypes.func,
  setCloseModal: PropTypes.func,
  currentTicket: PropTypes.object,
  formData: PropTypes.object,
  updateTicketFetch: PropTypes.func,
  updateTicketLoading: PropTypes.bool,
  setCurrentTicketState: PropTypes.func,
  ticketCommentsData: PropTypes.object,
  ticketCommentsLoading: PropTypes.bool,
  ticketLoading: PropTypes.bool,
  moveTicketFetch: PropTypes.func,
  moveTicketLoading: PropTypes.bool,
  commentMessage: PropTypes.object,
  setCommentMessage: PropTypes.func,
  sendTicketCommentsFetch: PropTypes.func,
  sendTicketCommentsLoading: PropTypes.bool,
  categories: PropTypes.array,
  assignees: PropTypes.array
};

const withTicketComments = withRequest(TicketCommentsService.getComments, {
  name: 'ticketComments',
  init: false,
  options: ({ ticket }) => ({ variables: _get(ticket, 'id') })
});

const withSendTicketComment = withRequest(TicketCommentsService.sendComment, {
  name: 'sendTicketComments',
  init: false,
  options: ({
    currentTicket, commentMessage,
    setCommentMessage, onClose, ticketCommentsFetch
  }) => ({
    variables: { id: _get(currentTicket, 'id'), ...commentMessage },
    onRequestFinished: () => {
      setCommentMessage(initialCommentState);
      ticketCommentsFetch(false);
    },
    onRequestError: err => {
      Tracker.hit('TicketModal - Closed');
      onClose(err, 'Erro ao enviar um comentrio para esse ticket');
    }
  })
});

const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false,
  options: ({ onClose, closeModal, currentTicket, setFormData }) => ({
    onRequestFinished: () => {
      if (closeModal) {
        Tracker.hit('TicketModal - Closed');
        onClose(null, `Ticket ${currentTicket.code} atualizado com sucesso`);
      }
    },
    onRequestError: error => {
      const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
      if (_isEmpty(errors)) {
        Tracker.hit('TicketModal - Closed');
        const errorMessage = _get(error, 'error', 'Erro ao atualizar esse ticket');
        onClose(error, errorMessage);
      } else {
        setFormData({ validationErrors: errors });
      }
    }
  })
});

const withTicket = withRequest(TicketsService.getTicket, {
  name: 'ticket',
  init: false,
  options: ({ ticket, setCurrentTicket, onClose }) => ({
    variables: _get(ticket, 'id'),
    onRequestFinished: data => {
      setCurrentTicket(_get(data, 'ticket'));
    },
    onRequestError: err => {
      Tracker.hit('TicketModal - Closed');
      onClose(err, 'Erro ao abrir esse ticket');
    }
  })
});

const withUpdateTicket = withRequest(TicketsService.updateTicket, {
  name: 'updateTicket',
  init: false,
  options: ({ currentTicket, onClose, closeModal, setFormData }) => ({
    variables: currentTicket,
    onRequestFinished: () => {
      if (closeModal) {
        Tracker.hit('TicketModal - Closed');
        onClose(null, `Ticket ${currentTicket.code} atualizado com sucesso`);
      }
    },
    onRequestError: error => {
      const errors = _mapValues(_get(error, 'errors', {}), (err) => err[0]);
      setFormData({ validationErrors: errors });
    }
  })
});

const initialFormData = { validationErrors: {} };
const initialCommentState = { content: '', isInternal: false };
export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormDataRaw', initialFormData),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withState('currentTicket', 'setCurrentTicket', {}),
  withState('commentMessage', 'setCommentMessage', initialCommentState),
  withState('closeModal', 'setCloseModal', false),
  withTicketComments,
  withSendTicketComment,
  withUpdateTicket,
  withMoveTicket,
  withTicket,
  lifecycle({
    componentDidMount() {
      const { ticket, setCurrentTicket, setCommentMessage } = this.props;
      setCurrentTicket(ticket);
      setCommentMessage(initialCommentState);
    }
  }),
  withPropsOnChange(['open'], ({
    open, setCommentMessage, setCurrentTicket, setFormData,
    ticketFetch, ticketCommentsFetch
  }) => {
    if (open) {
      setFormData(initialFormData);
      setCurrentTicket({});
      setCommentMessage(initialCommentState);
      ticketFetch(true);
      ticketCommentsFetch(true);
    }
  }),
  withHandlers({
    setCurrentTicketState: ({ setCurrentTicket, currentTicket }) => (attrs) => {
      setCurrentTicket({ ...currentTicket, ...attrs });
    }
  })
)(TicketModal);
