/* global window */
import React, { memo } from 'react';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { compose, lifecycle, withState } from 'recompose';
import _get from 'lodash/get';
import _set from 'lodash/set';
import Tracker from '../../utils/tracker';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import MemberService from '../../services/member';
import withRequest from '../../utils/with-request';
import Session from '../../utils/session';
import styles from './styles';
import config from '../../config';

const Slack = memo(({ classes, alert, setAlert, revokeSlackFetch, revokeSlackLoading }) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const userData = Session.getUserData();
  const slackUserId = _get(userData, 'member.slack_user_id');
  const slackTeamId = _get(userData, 'member.company.slack_team_id');
  const onAuthUserOnSlack = () => {
    // window.alert('Em breve lanaremos integrao com Slack');
    Tracker.hit('SlackIntegration - AuthUser');
    let url = `${config.slackOAuthURL}?scope=${config.slackOAuthScope}&team=${slackTeamId}`;
    url += `&client_id=${config.slackClientId}&user_scope=${config.slackOAuthUserScope}`;
    url += `&redirect_uri=${config.slackRedirectURL}`;
    window.location.href = url;
  };
  const onInstallLobbyOnSlack = () => {
    // window.alert('Em breve lanaremos integrao com Slack');
    Tracker.hit('SlackIntegration - InstallLobby');
    let url = `${config.slackOAuthURL}?scope=${config.slackOAuthScope}`;
    url += `&client_id=${config.slackClientId}&user_scope=${config.slackOAuthUserScope}`;
    url += `&redirect_uri=${config.slackRedirectURL}`;
    window.location.href = url;
  };
  const onRevokeSlack = () => {
    Tracker.hit('SlackIntegration - RevokeUser');
    revokeSlackFetch(true);
  };
  const isDisabled = revokeSlackLoading;
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <br />
        <div className={classes.container}>
          <Paper className={classes.paper}>
            <section className={classes.slackBox}>
              <div className={classes.textCenter}>
                <img
                  src={require('../../assets/images/slack-integration.png')}
                  alt="Integrao Lobby com Slack"
                  className={classes.slackIntegration}
                />
                <br />
                <br />
                <Typography variant="h6" component="strong">
                  <b>Crie tickets atravs do Slack</b>
                </Typography>
                <br />
                <Typography variant="body2" color="textSecondary">
                  <b>Leva menos de um minuto</b>
                </Typography>
              </div>
              <br />
              <div className={classes.textCenter}>
                <Typography variant="subtitle2">
                  Colaboradores podero criar tickets
                  <br />
                  via atalho Lobby ou comando
                  <b> /lobby </b>
                  no Slack
                </Typography>
                <br />
                {
                  slackTeamId ?
                    (
                      slackUserId ?
                        <Button
                          onClick={onRevokeSlack}
                          size="large"
                          color="default"
                          variant="outlined"
                          disabled={isDisabled}
                        >
                          Desconectar Slack
                        </Button>
                        :
                        <Button
                          onClick={onAuthUserOnSlack}
                          size="large"
                          color="primary"
                          variant="contained"
                          disabled={isDisabled}
                        >
                          Integrar Slack
                        </Button>
                    )
                    :
                    (
                      <Button
                        onClick={onInstallLobbyOnSlack}
                        size="large"
                        color="primary"
                        variant="contained"
                        disabled={isDisabled}
                      >
                        Instalar Lobby no Slack
                      </Button>
                    )
                }
              </div>
            </section>
          </Paper>
        </div>
      </section>
    </div>
  );
});

Slack.propTypes = {
  classes: PropTypes.object,
  alert: PropTypes.object,
  setAlert: PropTypes.func,
  revokeSlackFetch: PropTypes.func,
  revokeSlackLoading: PropTypes.bool
};

const withRevokeSlack = withRequest(MemberService.revokeSlackUser, {
  name: 'revokeSlack',
  init: false,
  options: ({ setAlert }) => ({
    onRequestFinished: async () => {
      setAlert({ alertMsg: 'A conta Slack foi desconectada', alertType: 'success', showAlert: true });
      const userData = Session.getUserData();
      _set(userData, 'member.slack_user_id', null);
      Session.setUserData(userData);
    },
    onRequestError: () => {
      setAlert({ alertMsg: 'Erro ao desconectar conta Slack', alertType: 'error', showAlert: true });
    }
  })
});

export default compose(
  withRouter,
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withRevokeSlack,
  lifecycle({
    componentDidMount() {
      const { location, setAlert } = this.props;
      const query = new URLSearchParams(location.search);
      const integrationStatus = query.get('status');
      if (integrationStatus) {
        if (integrationStatus === 'ok') {
          setAlert({ alertMsg: 'Conta Slack integrada com sucesso', alertType: 'success', showAlert: true });
          window.history.pushState('', '', '/#/configuracoes/slack');
        }
        if (integrationStatus === 'error') {
          setAlert({ alertMsg: 'Erro ao conectar conta Slack', alertType: 'error', showAlert: true });
          window.history.pushState('', '', '/#/configuracoes/slack');
        }
      }
    }
  })
)(Slack);
